    align-items: center;
    font-style: italic;
    font-weight: 700;
    color: #AA0601; }
    .series li .seriesTitle img {
      width: 48px;
      height: 48px;
      margin-right: 8px; }
    .series li .seriesTitle span {
      font-size: 48px;
      color: #AA0601;
      margin-right: 8px; }
  .series .sunglasses .seriesTitle {
