.coBrand {
  padding-top: 60px;
  background-image: url(../images/bg.png); }
  .coBrand .subTitle {
    color: #fff; }
  .coBrand ul {
    font-size: 72px;
    text-align: center; }
    .coBrand ul a {
      text-decoration: none;
      color: #AA0601;
      font-weight: 700; }
    .coBrand ul .coBrandItem {
      width: 50%;
      position: relative; }
      .coBrand ul .coBrandItem img {
        opacity: 0.5; }
      .coBrand ul .coBrandItem h3 {
        width: 400px;
        z-index: 1;
        position: absolute;
        top: 50%;
