.contact {
  padding-top: 60px;
  background-color: #AA06010D; }
  .contact .formWrap {
    position: relative;
    width: 66%;
    margin: 0 auto; }
    .contact .formWrap p {
      margin-bottom: 20px; }
    .contact .formWrap input {
      width: 100%;
      margin: 4px 0 16px;
      padding: 1rem;
      font-size: 16px; }
    .contact .formWrap textarea {
      margin-top: 4px;
      margin-bottom: 16px;
      width: 100%;
