  justify-content: space-between;
  padding: 30px 88px;
  font-size: 20px; }
  nav .logo {
    background-image: url(../images/logo.png);
    width: 80px;
    height: 48.57px;
    text-indent: 101%;
    overflow: hidden;
    white-space: wrap;
    display: block; }
    @media (max-width: 375px) {
      nav .logo {
        width: 100%; } }
  nav ul {
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around; }
    nav ul a {
      padding: 8px 0;
      text-decoration: none;
      color: #fff; }
  @media (max-width: 768px) {
    nav {
      padding: 30px 39px; } }
  @media (max-width: 375px) {
